body {
  font-family: Work Sans;
  text-align: center;
  background-color: rgb(143, 228, 234);
}

#root{
  background-color: rgb(143, 228, 234);
}

.head{
  display: flex;
  width: 100%;
}

.navbar-left{
  display: block;
  width: 25%;
}

.navbar-brand {
  text-align: left;
  display: block;
  align-content: center;
  padding-top: 2em;
  margin-bottom: 2em;
  
}

.navbar-brand a.nabar-item {
  width: auto;
}

.head-name{
  font-size: 2em;
  font-weight: 900;
}

.navbar-right{
  display: flex;
  justify-content: flex-end;
  width: 75%;
}

.nav{
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
}

.nav-tabs{
  display: flex;
  justify-content: flex-end;
  width: auto;
}

.nav-item{
  margin-left: 2em;
  margin-right: 2em;
  font-size: 1.2em;
  font-weight: bold;
}


main {
  margin-top: 2em;
  margin-bottom: 2em;
  margin-left: 30em;
  margin-right: 30em;
}

.my-pic {
	clip-path: circle();
  width: 250px;
}

.label {
  text-align: left;
}

footer.footer {
  background-color: rgb(255, 255, 255);
}




@media only screen and (max-width: 1024px) {
    main {
      margin-top: 2em;
      margin-bottom: 2em;
      margin-left: 2em;
      margin-right: 2em;
    }
}
